import provinces from './city-data/province.js';
import citys from './city-data/city.js';
export default {
	data() {
		return {
			result: [],
			data: {},
			checkArr: [],
			checkValue: [],
			pickVal: [],
			showPicker: false,
			resultStr: ""
		};
	},
	props: {
		themeColor: {
			type: String,
			default () {
				return "#f00"
			}
		},
		defaultVal: {
			type: Array,
			default () {
				return [0, 0, 0, 0, 0, 0]
			}
		}
	},
	methods: {
		maskTap() {
			this.showPicker = false;
		},
		show() {
			this.showPicker = true;
		},
		hide() {
			this.showPicker = false;
		},
		pickerCancel() {
			this.$emit("cancel", {
				checkArr: this.checkArr,
				defaultVal: this.pickVal
			});
			this.showPicker = false;
		},
		pickerConfirm(e) {
			this.$emit("confirm", {
				checkArr: this.checkArr,
				defaultVal: this.pickVal,
				result: this.resultStr
			});
			this.showPicker = false;
		},
		bindChange(val) {
			let _this = this;
			let arr = val.detail.value;
			let province, city;
			let checkArr = _this.checkArr;
			let mode = _this.mode;
			province = _this.data.provinces[arr[0]].label;
			city = _this.data.citys[arr[1]].label;
			if (province != checkArr[0]) {
				_this.data.citys = citys[arr[0]];
				arr[1] = 0;
				city = _this.data.citys[arr[1]].label;
			};
			_this.checkArr = [province, city];
			_this.checkValue = [_this.data.provinces[arr[0]].value, _this.data.provinces[arr[0]].value, ];
			_this.resultStr = province + city;
			_this.$nextTick(() => {
				_this.pickVal = arr;
			})
		},
		initData() {
			let _this = this;
			let data = {};
			let mode = _this.mode;
			let province, city;
			data = {
				provinces: provinces,
				citys: citys[_this.defaultVal[0]]
			};
			_this.data = data;
			province = data.provinces[_this.defaultVal[0]];
			city = data.citys[_this.defaultVal[1]];
			_this.checkArr = [province.label, city.label];
			_this.checkValue = [province.value, city.value];
			_this.resultStr = province.label + city.label;
			_this.$nextTick(() => {
				_this.pickVal = _this.defaultVal;
			})
		}
	},
	mounted() {
		this.initData();
	}
}
